<template>
  <div class="scenery">
    <TopImage :image="topImage" />
    <div class="">
      <Breadcrumb>
        <span>当前位置：</span>
        <BreadcrumbItem>实用信息</BreadcrumbItem>
        <BreadcrumbItem>景区美景</BreadcrumbItem>
      </Breadcrumb>
      <div class="image-wall">
        <div class="images-container">
          <div v-for="(item, index) in imageList" :key="index" :style="{backgroundImage: 'url('+ switchItem(item)[1] + ')'}" @click="handleImageClick(index)" class="image-item hvr-grow"></div>
        </div>
        <Page class="common-page" :total="pageTotal" :page-size="pageSize" @on-change="handlePageChange"></Page>
      </div>
    </div>
    <Modal v-model="modal" width="80%" class-name="vertical-center-modal">
      <div style="text-align:center">
        <swiper :options="swiperOption" ref="imageSwiper">
          <swiper-slide class="slide-1" v-for="(item, index) in imageList" :key="index">
            <div :style="{backgroundImage: 'url('+ switchItem(item)[1] + ')'}" class="swiper-item"></div>
          </swiper-slide>
          <div class="swiper-button-prev" slot="button-prev"></div>
          <div class="swiper-button-next" slot="button-next"></div>
        </swiper>
      </div>
      <div slot="footer" class="modal-footer">
        <div class="swiper-pagination images-pagination" slot="pagination"></div>
      </div>
    </Modal>
  </div>
</template>

<script>
import TopImage from "@/components/TopImage.vue";
import TopBanner from "../assets/sceneryBanner.png";
import config from "../config";
export default {
  data() {
    return {
      topImage: TopBanner,
      pageSize: 20,
      pageTotal: 0,
      imageList: [],
      wallHeigth: 600,
      modal: false,
      swiperOption: {
        spaceBetween: 30,
        effect: "fade",
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        }
      }
    };
  },
  methods: {
    handlePageChange(page) {
      var _this = this;
      _this.$Spin.show();
      this.$http
        .get(config.ApiRoot + "/scenery/", {
          params: {
            page: page,
            pageCount: _this.pageSize
          }
        })
        .then(function(res) {
          if (res.data.code === 0) {
            _this.imageList = res.data.list;
            _this.pageCount = res.data.count;
            _this.$Spin.hide();
          }
        })
        .catch(function(error) {
          _this.$Spin.hide();
          _this.$Notice.error({
            title: "服务器错误",
            desc: "请您稍后再试。"
          });
        });
    },
    handleImageClick(index) {
      this.modal = true;
      this.$refs.imageSwiper.swiper.slideTo(index, 1000, false);
    },
    switchItem(item) {
      return item.split("|");
    }
  },
  components: {
    TopImage
  },
  mounted() {
    this.handlePageChange(1);
  }
};
</script>

<style lang="scss" scoped>
.image-wall {
  background-color: #161616;
  .common-page {
    padding: 20px 0;
  }
}
.images-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  .image-item {
    height: 160px;
    width: calc(20% - 8px);
    margin: 10px 4px 0 4px;
    cursor: pointer;
    background-repeat: no-repeat;
    background-size: 100% 100%;
  }
}
.vertical-center-modal {
  display: flex;
  align-items: center;
  justify-content: center;

  .ivu-modal {
    top: 0;
  }
  .images-pagination {
    left: 0;
    right: 0;
    bottom: 2px;
  }
}
.swiper-item {
  height: 500px;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #fff;
  background-size: 100% 100%;
}
</style>





